<template>
  <v-navigation-drawer
    v-model="drawer"
    app
    dark
    temporary
  >
    <v-list>
      <v-list-item>
            <v-btn
              color="secondary"
              fab
              dark
              small
              @click="toggleDrawer"
            >
              <v-icon>mdi-backburger</v-icon>
            </v-btn>
      </v-list-item>

          
      <v-list-item
        v-for="(link, i) in drawer_items"
        :key="i"
        :to="link.to"
        :href="link.href"

      >
     
        <v-list-item-title v-text="link.text" />
      </v-list-item>
    </v-list>
  </v-navigation-drawer>
</template>

<script>
  // Utilities
  import {
    mapGetters,
    mapMutations,
    mapState
  } from 'vuex'

  export default {
    name: 'CoreDrawer',

    computed: {
      ...mapState(['drawer_items']),
      drawer: {
        get () {
          return this.$store.state.drawer
        },
        set (val) {
          this.setDrawer(val)
        },
      }
    },

    methods: {
      ...mapMutations(['setDrawer', 'toggleDrawer']),
      
      onClick (e, item) {
        e.stopPropagation()

         console.log('this.links');

        if (item.to === '/') {
          this.$vuetify.goTo(0)
          this.setDrawer(false)
          return
        }

        if (item.to || !item.href) return

        this.$vuetify.goTo(item.href)
        this.setDrawer(false)
      },
    }
  }
</script>
